<script>
export default {
  props: ['search'],
  data() {
    return {
      appList: {

      },
      nowSelectAlias: '', //当前选中的别名
      isResponse: 0,  //是否有请求结果了

    };
  },
  methods: {
    // 跳转到详情页面
    goDetail(id) {
      this.$router.push(`/appmanage/detail?appid=${id}`);
    },

    /**
     * 根据别名请求应用列表
     * @param {String} platform_alias 别名
     * @param {String} type 已上线 online 未上线 noline
     */
    async getAppListByAlias({ platform_alias, type, loadmore = false }) {
      // 将当前显示的内容切换为
      this.nowSelectAlias = platform_alias;
      let compony_id = this.$zero.getCompany_id();
      let user_id = this.$zero.getUserid();
      let pageIndex;

      if (!this.appList[platform_alias]) {
        this.$set(this.appList, platform_alias, {
          online: { data: [], pageIndex: 1 },
          noline: { data: [], pageIndex: 1 },
          stop: { data: [], pageIndex: 1 },
          num: 0, // 请求到的个数
        });
      }

      var status;
      if (type === 'online') {
        status = 1;
      } else if (type === 'noline') {
        status = 0;
      } else if (type === 'stop') {
        status = 2;
      }

      // 在这里再加上如果是加载更多则让 pageIndex + 1
      if (loadmore) {
        // 加载更多
        pageIndex = this.appList[platform_alias][type].pageIndex + 1;
        // this.appList[platform_alias][type].pageIndex++;
      } else {
        // 非加载更多
        pageIndex = this.appList[platform_alias][type].pageIndex;
      }

      // 开始请求
      // 判断有一种情况是不请求的
      if (this.appList[platform_alias][type].pageIndex !== 1) {
        // 第1页
        if (pageIndex === this.appList[platform_alias][type].pageIndex) {
          return;
        }
      }

      /*
        这里还有的问题 是什么时候去请求下一页应用 下一页查询的条件是什么
      */
      let appList = await this.$api.application.getAppList({
        company_id: compony_id,
        platform_alias: platform_alias,
        pageIndex: this.appList[platform_alias][type].pageIndex,
        status,
        user_id,
      });

      // 修改当前请求页码数
      this.appList[platform_alias][type].pageIndex++;

      console.log('请求结果');
      this.appList[platform_alias].num++;

      console.log(appList);
      if (appList.statusCode === 75500) {
        return this.$zero.ToastError('请求失败');
      }
      if (appList.statusCode !== 75200) {
        return this.$zero.ToastError(`请求应用列表失败，错误码:${appList.statusCode}`);
      }

      // 请求成功
      this.appList[platform_alias][type].data = this.appList[platform_alias][type].data.concat(appList.apps);
      console.log('最终结果为');
      console.log(this.appList);
    },
  },

  watch: {
    'search'() {
      // console.log('触发了');
      // console.log(this.search);
      console.log('这里监听到了变化吧');
      this.getAppListByAlias({ platform_alias: this.search, type: 'online' });
      this.getAppListByAlias({ platform_alias: this.search, type: 'noline' });
      this.getAppListByAlias({ platform_alias: this.search, type: 'stop' });
    },
  },
  created() {
    this.getAppListByAlias({ platform_alias: this.search, type: 'online' });
    this.getAppListByAlias({ platform_alias: this.search, type: 'noline' });
    this.getAppListByAlias({ platform_alias: this.search, type: 'stop' });
  }
};
</script>
